const cardDescriptions = document.querySelectorAll(".card__description");
const showMoreButtons = document.querySelectorAll(".card__show-more");
const navbar = document.querySelector(".navbar__container");
const illustration = document.querySelector(".hero__illustration-container");
const heroText = document.querySelector(".hero__text-container");
const productCards = document.querySelectorAll(".card");
const showCardsButton = document.querySelector(".show-cards-button");

ScrollOut({
    threshold: .3,
    once: true
});


showMoreButtons.forEach((btn, i) => {
    btn.addEventListener("click", (e) => {
        if (btn.textContent === "Read More") {
            cardDescriptions[i].style.height = "auto";
            btn.textContent = "Show Less";
        } else {
            cardDescriptions[i].style.height = "1.5em";
            btn.textContent = "Read More";
        }

    })
})

window.addEventListener("scroll", () => {
    illustration.style.marginTop = (320 - window.pageYOffset / 3).toString() + "px";
    //heroText.style.marginTop = (50 + window.pageYOffset / 2).toString() + "px";


    if (window.pageYOffset > 100 && window.pageYOffset <= 560) {
        illustration.style.visibility = "visible"
        if (!navbar.classList.contains("is-scrolling")) { navbar.classList += " is-scrolling"; }

    }

    if (window.pageYOffset <= 100) {
        navbar.classList.remove("is-scrolling");
    }
})

if (productCards.length > 3) {
    showCardsButton.style.display = "inline-block";
    productCards.forEach((card, i) => {
        if (i > 2) {
            productCards[i].style.display = "none";
        }
    })
} else {
    showCardsButton.style.display = "none";
}
showCardsButton.addEventListener("click", () => {
    productCards.forEach((card, i) => {
        if (i > 2) {
            productCards[i].style.display = "grid";
            //animate in with ScrollOUt

        }
        showCardsButton.style.display = "none";
    })
    ScrollOut().update();
})

//make svg mimic object-fit cover on small screens
let projVector = document.querySelector(".projects__vector");

window.onload = () => {
    if (window.innerWidth < 900) {
        projVector.setAttribute("preserveAspectRatio", "xMinYMid slice");
    }
}

window.onresize = () => {

    if (window.innerWidth < 900) {
        projVector.setAttribute("preserveAspectRatio", "xMinYMid slice");
    } else {
        projVector.setAttribute("preserveAspectRatio", "none");
    }
}

//smooth scroll code
init();
function init() {
    new SmoothScroll(document, 120, 12)
}

function SmoothScroll(target, speed, smooth) {
    if (target == document)
        target = (document.documentElement || document.body.parentNode || document.body) // cross browser support for document scrolling
    var moving = false
    var pos = target.scrollTop
    target.addEventListener('mousewheel', scrolled, { passive: false })
    target.addEventListener('DOMMouseScroll', scrolled, { passive: false })

    function scrolled(e) {
        e.preventDefault(); // disable default scrolling

        var delta = normalizeWheelDelta(e)

        pos += -delta * speed
        pos = Math.max(0, Math.min(pos, target.scrollHeight - target.clientHeight)) // limit scrolling

        if (!moving) update()
    }

    function normalizeWheelDelta(e) {
        if (e.detail) {
            if (e.wheelDelta)
                return e.wheelDelta / e.detail / 40 * (e.detail > 0 ? 1 : -1) // Opera
            else
                return -e.detail / 3 // Firefox
        } else
            return e.wheelDelta / 120 // IE,Safari,Chrome
    }

    function update() {
        moving = true
        var delta = (pos - target.scrollTop) / smooth
        target.scrollTop += delta
        if (Math.abs(delta) > 0.5)
            requestFrame(update)
        else
            moving = false
    }

    var requestFrame = function () { // requestAnimationFrame cross browser
        return (
            window.requestAnimationFrame ||
            window.webkitRequestAnimationFrame ||
            window.mozRequestAnimationFrame ||
            window.oRequestAnimationFrame ||
            window.msRequestAnimationFrame ||
            function (func) {
                window.setTimeout(func, 1000 / 50);
            }
        );
    }()
}